/*****Theme blue Dark*******/
.wrapper.bg-dark-themeBlueDark {
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#0b066d+20,2989d8+100 */
    background: #0b066d;
    /* Old browsers */
    background: -moz-linear-gradient(top, #0b066d 20%, #2989d8 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #0b066d 20%, #2989d8 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #0b066d 20%, #2989d8 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0b066d', endColorstr='#2989d8', GradientType=0);
    /* IE6-9 */
}

.bg-dark-themeBlueDark .bg-themeBlueDark-light {
    background-color: rgb(0 0 0 / 50%);
}

.border-themeBlueDark-md-light {
    border-color: rgb(0 0 0 / 50%)
}

.bg-dark-themeBlueDark select.bg-themeBlueDark-light {
    color: #fff
}

.bg-dark-themeBlueDark .bg-themeBlueDark-card {
    background-color: rgb(0 0 0 / 34%);
}

.bg-dark-themeBlueDark .text-themeBlueDark {
    color: #fff
}

.bg-dark-themeBlueDark .text-dark-theme {
    color: #000
}

.bg-dark-themeBlueDark .bg-themeBlueDark-card h2,
.bg-dark-themeBlueDark .sidebar-list {
    color: #fff
}

.bg-dark-themeBlueDark .bg-themeBlueDark-card [type="radio"]+label {
    color: #fff
}

.bg-dark-themeBlueDark .notification {
    color: #fff
}

.bg-dark-themeBlueDark .text-active-themeBlueDark,
.hover-text-themeBlueDark:hover,
.bg-dark-themeBlueDark .text-themeBlueDark-dark {
    color: #0B066D
}
.border-themeBlueDark {
    border-color: #0a1f3c
}

.bg-dark-themeBlueDark .input-text:focus+.label-helper {
    background-color: rgb(255 255 255 / 0%);
    bottom: auto;
    top: -11px;
}

.bg-dark-themeBlueDark .label-helper.visible {
    opacity: 1;
    bottom: auto;
    top: -11px;
    background-color: #ffffff00;
    font-family: arial;
    font-size: 12px;
    line-height: 1;
    opacity: 1;
    padding: 2px;
    font-weight: 600;
}

/* .MuiButtonBase-root.MuiIconButton-root {
    color: #fff
  } */
.bg-dark-themeBlueDark .css-1qgma8u-MuiButtonBase-root-MuiTableSortLabel-root.Mui-active .MuiTableSortLabel-icon {
    color: #fff
}

